.chatList {
  height: 100%;
  display: flex;
  flex-direction: column;


  .dialog {
    overflow-y: scroll;
    padding: .5em;
    height: 100%;
    margin: 1em 0 .25em;
    &__event {
      text-align: center;
      margin-bottom: 1em;
      &__date {
        display: block;
        font-size: .8em;
        margin-bottom: 3px; } }

    &__manager, &__user {
      width: 75%;
      position: relative;
      padding: .4em;
      margin-bottom: 1em;
      font-size: 1.2em;
      &:after {
        content: '';
        position: absolute;
        bottom: -16px;
        border: 10px solid transparent; } }

    &__manager {
      background-color: #EDEDED;
      &:after {
        left: 0;
        border-left: 10px solid #EDEDED;
        border-top: 10px solid #EDEDED; }
      &__date {
        display: block;
        font-size: .6em;
        margin-bottom: 3px; } }

    &__user {
      background-color: #45697D;
      color: #fff;
      transform: translateX(85px);
      text-align: right;
      &:after {
        right: 0;
        border-top: 10px solid #45697D;
        border-right: 10px solid #45697D; }
      &__date {
        display: block;
        font-size: .6em;
        margin-bottom: 3px; } }

    .sidecarLInk {
      color: blue;
      cursor: pointer; } } }



.chatList_footer {
  display: flex;
  align-items: center;
  font-size: 1em;
  border-top: 1px solid #a9a4a4;
  padding: .35em 0;

  .showPanel {
    width: 35px;
    height: 28px;
    background-color: transparent;
    border: none;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-image: url('../../assets/img/sprites_v1.png');
      background-size: auto 78px;
      background-repeat: no-repeat;
      background-position: -501px 3px; }
    &__showed {
      &:before {
        background-position: -462px 2px; } } }

  .sendTextInput {
    flex-grow: 1;
    font-style: italic;
    padding: 0.5em;
    border: none; }

  .sendMessageButton {
    width: 35px;
    height: 28px;
    cursor: pointer;
    border: none;
    background-color: transparent;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-image: url('../../assets/img/sprites_v1.png');
      background-size: auto 78px;
      background-repeat: no-repeat;
      background-position: -540px 4px; } } }

.panel {
  min-height: 160px;
  padding: 1.5em 1em;
  border-top: 2px solid #a9a4a4;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    left: 0;
    top: .8em;
    border-top: 1px solid #a9a4a4; }

  .buttonClosePanel {
    background-color: transparent;
    border: none;
    font-size: 0.6em;
    text-transform: none;
    position: relative;
    padding-top: 20px;
    color: #6d6e71;
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      width: 25px;
      height: 100%;
      top: 0;
      left: 50%;
      background-image: url('../../assets/img/sprites_v1.png');
      background-size: auto 80px;
      background-repeat: no-repeat;
      background-position: -119px 0;
      transform: translateX(-50%); } } }

.addFile {
  &__input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1; }
  &__label {
    font-size: 2em;
    padding: 0 0.4em;
    cursor: pointer; } }


